body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.author-name-date{
  margin-bottom: 0;
}
.author-name-date span{
  margin-left: 10px;
  margin-right: 10px;
  position: relative;
  color: #333;
  text-decoration: none;
}
.author-name-date span a{
  color: #333;
  text-decoration: none;
}
.author-name-date span a:hover{
  color: #f47526;
  text-decoration: none;
}
.author-name-date span:not(:first-child)::before {
  content: "|";
  color: rgba(51, 51, 51, .5);
  font-size: 16px;
  font-weight: 600;
  left: -14px;
  position: absolute;
}
.sticky-post-title-link{
  color: #333;
  text-decoration: none;
}
.sticky-post-title-link:hover{
  color: #f47526;
  text-decoration: none;
}
.sticky-post-title{
font-size: 34px;
  line-height: 51px;
  font-weight: 600;
  margin-bottom: 20px;
}
.regular-post-wrapper{
  padding: 0px 15px 10px;
}
.post-title-link{
  color: #333;
  text-decoration: none;
}
.post-title-link:hover{
  color: #f47526;
  text-decoration: none;
}
.post-title-link .post-title{
  font-size: 32px;
  font-weight: 500;
  line-height: 48px;
  margin-bottom: 20px;
  max-width: 600px;
}
  .regular-post-wrapper .author-name-date span:first-child{
    margin-left: 0px;
  }
  .listing-inner-wrapper p {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #333;
}
