.category-list-filter .category-list{
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  list-style: none;
  margin-top: 50px;
  margin-bottom: 40px;
  align-items: center;
}
.category-list-filter  .category-list li{
  margin-left: 20px;
  margin-right: 20px;
}
.category-list-filter  .category-list li button, .category-list-filter  .category-list li a{
  padding: 12px;
    border: 1px solid #d2d5d9;
    border-radius: 6px;
    text-decoration: none;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #333;
    background-color: transparent;
}
.category-list-filter  .category-list .category-item.active button{
  border: 1px solid #333;
  color: #fff;
  background-color: #333;
}
.category-list-filter  .category-list li button:hover, .category-list-filter  .category-list li a:hover{
    border: 1px solid #333;
    color: #fff;
    background-color: #333;
}