.comment {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.comment-avatar {
  margin-right: 10px;
}

.avatar-circle {
  width: 50px;
  height: 50px;
  background-color: #007bff;
  color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
}

.comment-body {
  flex-grow: 1;
}

.comment-author {
  font-weight: bold;
  margin-bottom: 0.2rem;
}

.comment-date {
  font-size: 12px;
  color: gray;
  margin-bottom: 8px;
}

.nested-comments {
  list-style: none;
  padding-left: 2rem;
}
.blog-content-wrapper .sab-alert-primary {
  color: #333333;
  background-color: #FFF3F3;
  border-color: #ffe0e0;
}
.sab-alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}
.blog-content-wrapper .sab-alert-primary .close{
  display: none;
}
.blog-content-wrapper .wp-block-image img{
  border: 1px solid #d9d9d9;
    padding: 5px;
    border-radius: 6px;
}
.cta-with-bg-pattern {
  background-image: url('pattern-image-cta-bg.png');
  width: 100%;
  background-repeat: no-repeat;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  background-size: 100% auto;
  text-align: center;
  padding-bottom: 12px;
  background-position: center center;
  margin-bottom: 15px;
}
.cta-with-bg-pattern span {
  display: block;
  font-size: 22px;
  font-weight: 600;
  line-height: 33px;
  text-align: center;
  color: #333;
  margin-top: 15px;
  margin-bottom: 12px;
}
.author-info{
  border: 1px solid rgba(51, 51, 51, .3);
    padding: 18px 12px;
    border-radius: 6px;
}
.author-info img{
  width: 75px;
  height: 75px;
  margin-bottom: 15px;
}
.author-info h4{
  font-size: 18px;
  line-height: 1.35;
    color: #222;
    font-weight: 500;
    margin-bottom: 10px;
}
.author-info p{
  margin-bottom: 20px;
  font-size: 15px;
  line-height: 1.6;
  color: #6C6B6B;
}
