.search-container {
  position: relative;
}

.search-toggle-btn {
  cursor: pointer;
}

.search-form {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  opacity: 0;
  transition: width 0.4s ease-in-out, opacity 0.4s ease-in-out;
  overflow: hidden;
  display: flex;
}

.search-form.open {
  width: 300px; /* Set width when opened */
  opacity: 1;
}

.search-form input {
  width: 100%;
}

.search-form button {
  margin-left: 5px;
}
