/* src/components/Footer.css */
  .site-footer {
    background-color: #303030;
    border-top: 6px solid #fff;
    padding-top: 60px;
  }
  .footer-logo {
    margin-bottom: 26px;
}
  .site-footer .footer-address-title {
    font-size: 17px;
    font-weight: 600;
    line-height: 25.5px;
    color: #fff;
    margin-bottom: 12px;
}
.site-footer .address-icon {
  max-width: 24px;
  margin-right: 8px;
}
  .site-footer .footer-address-text {
    margin-bottom: 20px;
    max-width: 330px;
    line-height: 22.4px;
    font-size: 16px;
    font-weight: 400;
    color: #fff;
}
.footer-phone {
  text-decoration: none;
  line-height: 22.4px;
  font-size: 16px;
  font-weight: 400;
  color: #fff;

}
.footer-menu-title{
  font-size: 20px;
  font-weight: 600;
  line-height: 30px;
  color: #fff;
  margin-bottom: 18px;
}
.footer-menu{
  list-style: none;
    padding: 0;
    margin: 0;
}
.footer-menu li{
  padding: 0;
  margin: 0 0 5px;
}
.footer-menu li a{
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.6px;
  color: #fff;
}
.footer-menu li a:hover{
  color: #f47526;
}
.footer-social-rating, .social-media ul {
  list-style: none;
  margin: 0 0 23px;
  padding: 0;
  display: flex;
}
.footer-social-rating li {
  max-width: 74px;
  background-color: #fff;
  border-radius: 5px;
  margin-right: 5px;
}
.social-media ul li {
  margin-right: 14px;
}
.social-media ul li a i {
  color: #b2b3b3;
  font-size: 28px;
}
.social-media ul li a:hover i {
  color: #f47526;
}
.footer-copy {
  background-color: rgba(0, 0, 0, .3);
}
.footer-copy-text p {
  font-size: 14px;
  line-height: 20px;
  color: #a3a3a3;
  font-weight: 400;
    margin: 17.5px 0;
}
.footer-phone:hover{
  color: #f47526;
}
.footer-copy-links{
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer-copy-links li{
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
}
.footer-copy-links li a {
  font-size: 14px;
  font-weight: 400;
  line-height: 13.01px;
  color: #a3a3a3;
  text-decoration: none;
}
.footer-copy-links li a:hover{
  color: #f47526;
}