.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.toc {
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
.toc .toc-toggle{
  padding: 15px 0px;
  display: block;
  width: 100%;
  text-align: left;
  text-decoration: none;
  color: #333333;
  font-size: 20px;
  font-weight: 500;
  border: 0;
  background-color: transparent;
}
.toc .toc-toggle:hover{
  text-decoration: none;
}
.toc .toc-toggle svg{
  float: right;
  margin-top: 3px;
  border: 1.5px solid rgba(0, 0, 0, .2);
  border-radius: 6px;
  padding: 2px;
  font-size: 16px;
}
.toc .toc-content {
  list-style: none;
  text-align: left;
  padding: 10px 2px 0px 10px;
  max-height: 400px;
  overflow-y: scroll;
  border: 1px solid #ccc;
}
.toc .toc-content li {
  padding: 0;
  margin: 0;
}
.toc .toc-content li button {
  background: none;
  border: none;
  color: #333333;
  cursor: pointer;
  text-decoration: none;
  text-align: left;
  margin-bottom: 10px;
  border-left: 2px solid rgba(51, 51, 51, .2);
}
.toc .toc-content li button:hover {
  text-decoration: none;
  color: #f47526;
}
.toc-level-2 {
  margin-left: 20px;
}
.toc-level-3 {
  margin-left: 40px;
}
/* Add more levels if needed */
.post-detail-container img{
  max-width: 100%;
  height: auto;
}
table.has-fixed-layout{
  width: auto;
  display: inline-block;
}
table.has-fixed-layout th,table.has-fixed-layout td{
  border: 1px solid #a8a8a8;
  padding: 5px;
}
.regular-post-main .regular-post-col:nth-child(odd){
  border-right: 1px solid #d2d5d9;
}
.pagination .page-item.active .page-link{
  background-color: #f47526;
  border-color: #f47526;
  color: #fff;
}
.pagination .page-item .page-link{
  color: #f47526;
  border-color: #f47526;
}
.pagination .page-item .page-link:focus{
  box-shadow: none;
}
.cat-hero-section{
  background: linear-gradient(140deg, #f8f7fd 0, #f7e9e9 55%, #ffaf5a 100%);
  padding: 100px 0;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 30px;
  margin-top: 40px;
  margin-bottom: 20px;
  position: relative;
}
.pagination-main ul li{
  padding: 10px 4px;
}
.breadcrumbs{
  list-style: none;
  margin: 0 0 20px;
  padding: 0;
  overflow: hidden;
}
.breadcrumbs li {
  margin-right: 5px;
  display: inline-block;
  vertical-align: middle;
  padding: 0 10px;
  position: relative;
}
.breadcrumbs li::before {
  content: "/";
  position: absolute;
  left: -5px;
  top: 0px;
  bottom: 0;
  margin: auto;
  color: #333;
  font-size: 14px;
  font-weight: 400;
}
.breadcrumbs li:first-child::before {
  content: "";
}
.breadcrumbs li, .breadcrumbs li a {
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  color: #333;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  line-height: 1.35;
  color: rgb(34, 34, 34);
  font-weight: 600;
  margin: 0px 0px 8px;
}
h2.wp-block-heading{
  font-size: 1.688rem;
  margin-bottom: 15px;
}
h3.wp-block-heading{
  font-size: 1.5rem;
  margin-bottom: 15px;
}
.blog-content-wrapper p{
  margin: 0px 0px 15px;
  color: #444444;
  line-height: 1.7;
  font-size: 18px;
}
.blog-content-wrapper a{
  line-height: 1.7;
  font-size: 18px;
  text-decoration: none;
}
.blog-content-wrapper ul{
padding-left: 1.2rem;
}
.blog-content-wrapper ul li{
  color: #444444;
  font-size: 18px;
  line-height: 1.7;
}
.blog-content-wrapper .wp-block-code code{
  font-size: 16px;
  display: inline-block;
  margin-top: 0px;
  color: #333;
  font-style: italic;
  background-color: rgba(217, 217, 217, .5);
  line-height: 20px;
  padding: 5px 10px;
}
.bg-css-pattern-main {
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  background: linear-gradient(45deg, #0000 calc(25% / 3), #fff 0 calc(50% / 3), #0000 0 calc(250% / 3), #fff 0 calc(275% / 3), #0000 0), linear-gradient(45deg, #fff calc(25% / 3), #0000 0 calc(50% / 3), #fff 0 25%, #0000 0 75%, #fff 0 calc(250% / 3), #0000 0 calc(275% / 3), #fff 0), linear-gradient(-45deg, #0000 calc(25% / 3), #fff 0 calc(50% / 3), #0000 0 calc(250% / 3), #fff 0 calc(275% / 3), #0000 0), linear-gradient(-45deg, #fff calc(25% / 3), #0000 0 calc(50% / 3), #fff 0 25%, #0000 0 75%, #fff 0 calc(250% / 3), #0000 0 calc(275% / 3), #fff 0) #333;
  background-size: 60px 60px;
  background-position: 0 0, 30px 30px;
}
.bg-css-pattern-wrapper {
  width: 100%;
  height: 100%;
  background: #fff;
  background: linear-gradient(31deg, #fff 38%, rgba(255, 255, 255, .85) 100%);
}
.bg-css-pattern-main span {
  display: block;
  font-size: 22px;
  font-weight: 600;
  line-height: 33px;
  text-align: center;
  color: #333;
  margin-bottom: 12px;
}
.bg-css-pattern-main p, .cta-with-bg-pattern p {
  display: block;
  font-size: 18px;
  font-weight: 400;
  line-height: 25.36px;
  text-align: center;
  color: #656565;
  margin-top: 0;
  margin-bottom: 12px;
}
.bg-css-pattern-main .middle-cta-link, .cta-with-bg-pattern .middle-cta-link {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  background: linear-gradient(90deg, #f47526 0, #ff512f 100%);
  transition: background .3s;
  margin: 0 auto;
  padding: 8px 38px;
  border-radius: 6px;
  font-weight: 600;
}
.bg-css-pattern-main .middle-cta-link:hover, .cta-with-bg-pattern .middle-cta-link:hover, .middle-cta-btn:hover {
  background: linear-gradient(90deg, #ff512f 0, #f47526 100%);
}
.leave-reply-form,.reply-form{
  border: 1px solid rgba(51, 51, 51, .5);
    border-radius: 6px;
    margin-bottom: 30px;
    margin-top: 20px;
    padding: 21px;
}
.leave-reply-form h3{
  font-size: 20px;
  font-weight: 600;
  line-height: 30px;
  color: #333;
  margin-bottom: 20px;
}
.leave-reply-form label, .reply-form label{
  display: block;
    margin-bottom: 6px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
}
.leave-reply-form input,.leave-reply-form textarea,.reply-form input,.reply-form textarea{
  -webkit-transition: .3s ease-in-out;
    -moz-transition: .3s ease-in-out;
    -ms-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    outline: 0;
    width: 100%;
    background-color: #fff;
    border: 1px solid rgba(51, 51, 51, .5);
    border-radius: 6px;
    max-width: 100%;
    padding: 10px;
    font-size: 14px;
    color: #333;
    line-height: 22px;
    margin-bottom: 20px;
}
.leave-reply-form button, .reply-form button{
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  padding: 8px 35px;
  color: #fff;
  border: 0;
  background-color: #f47526;
    border-radius: 6px;
    display: block;
    text-align: center;
}
.sab-alert-warning {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}
.wp-block-simple-alerts-for-gutenberg-alert-boxes button{
  display: none;
}
.post-author-readingtime{
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.post-author-readingtime p{
  margin-bottom: 0;
  display: flex;
  align-items: center;
  font-size: 18px;
  color: #333333;
}
.hero-ellipse {
  height: 4px;
  width: 4px;
  background-color: #333;
  border-radius: 50%;
  display: inline-block;
  margin-left: 15px;
  margin-right: 15px;
}
.comment-item{
  display: flex;
  margin-top: 20px;
  border-top: 1px solid rgba(51, 51, 51, .5);
  padding-top: 20px;
}
.comment-item:first-child{
border-top: 0;
margin-top: 10px;
padding-top: 10px;
}
.comment-avatar-img{
  width: 42px;
